
@media screen and (min-width: 670px){
  #header-body{
    display: flex;
    justify-content: center;
    background-image: linear-gradient(to right,rgb(128, 240, 206),rgb(162, 216, 233),rgb(245, 196, 202));
  }
  header{
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
  }
  .site-logo img{
    width: 60px;
    border-radius: 7px;
    transform: rotateY(180deg);
  }
  .navs{
    display: flex;
    justify-content: space-between;
    margin: 0 100px
  }
  .navs div{
    margin: 0 20px
  }
}
@media screen and (max-width: 670px){
  #header-body{
    display: flex;
    justify-content: center;
    background-image: linear-gradient(to right,rgb(128, 240, 206),rgb(162, 216, 233),rgb(245, 196, 202));
  }
  header{
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .site-logo img{
   width: 40px;
   border-radius: 5px;
  }
  .navs{
    display: flex;
    justify-content: space-between;
    margin: 0 40px
  }
  .navs div{
    margin: 0 10px
  }

}