@media screen and (min-width: 670px){
  .prj-img img{
    width: 100%;
    display: block;
  }
  .prj-item{
    border: solid 2px #52addd;
    border-radius: 4px;
    padding: 10px;
  }
  .prj-img{
    border: solid 1px #52addd;
    margin-bottom: 10px;
  }
  .prj-readme{
    color: #77787a;
  }
}

@media screen and (max-width: 670px){
  .prj-img img{
    width: 100%;
    display: block;
  }
  .prj-img{
    border: solid 1px #52addd;
    margin-bottom: 10px;
  }
  .prj-item{
    background-color: rgb(250, 246, 242);
    border-radius: 4px;
    padding: 10px;
  }
  .prj-readme{
    color: #77787a;
    padding: 10px;
  }
}