
.article-item{
  border: solid 1px white;
  background-color: white;
  padding: 6px;
  margin: 10px 0;
}

.article-item .img-title{
  margin: 0 0 10px 10px;
  display: flex;
  justify-content: space-between;
}
.img-container{
}
.img-container img.tumbnail{
  width:100px;
  border-radius: 5px;
  object-fit:cover;
}

.article-item .title-date{
  margin: 20px 0 10px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.article-item .title-date .title{
  color: black;
  text-decoration: underline;
}
.article-item .title-date .title:hover{
  color: rgb(116, 111, 111);
}
.article-item .title-date .last-modified{
  font-size: .7rem;
  color: #666
}
.article-item .tags{
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  margin: 0 0 0 10px;
}
.tag-item{
  margin: 5px 4px
}
.show-tab-article{
  display: block;
}
.hide-tab-article{
  display: none;
}
@media screen and (min-width: 670px){
  .article-item{
    border-top: solid 2px #eee;
  }
  .article-item:first-child{
    border: none;
  }
  .article-item:hover .title{
     color:#1077af
  }
  .tab-nav-article{
    display: flex;
    justify-content: flex-start;
    background-color: white;
  }
  .tab-nav-article>div{
    border:solid 1.2px white;
    padding: 3px 6px;
    margin: 0 5px;
    border-radius: 2px;
  }
  .tab-nav-article>div:hover{
    cursor: pointer;
  }
  .selected-tab-article{
    background-color: #1077af;
    color: white;
  }
  .not-selected-tab-article{
    color: #888;
    background-color: white;
  }
}
@media screen and (max-width: 670px){
  .articles-list{
    background-color: #eee;
  }

}