#title{
  margin: 3px 10px;
}

#back-home{
  color: rgb(9, 99, 91);
}
#back-home:hover{
  text-decoration: underline;
  cursor: pointer;
}
.topic-article-link, .topic-article-link-selected{
  padding: 5px 0;
}
.topic-article-link-selected a{
  color: rgb(17, 14, 197)
}
.topic-article-link-selected::before{
  content:'>>'
}
.topic-article-link a{
  color: black
}
.topic-article-link a:hover{
  text-decoration: underline;
}
@media screen and (min-width: 670px){
  #sider-menu{
    background-color: rgb(171, 241, 235);
    position: fixed;
    top:0;
    height: 100%;
    width: 250px;
    padding: 10px 20px;
    overflow-y: scroll;
  }
  #header{
    margin-left: 250px;
  }
  
  #article-info{
    margin-left: 250px;
   }
  #article-content{
    padding: 20px 100px;
   }
   #article-content img{
    width: 100%;
    margin: auto;
  }
  #tpc-article-vcomments{
    width: 600px;
    margin: auto;
  }
  #loading{
    width: 500px;
    margin: auto;
  }
}
@media screen and (max-width: 670px){
  #article-content{
   padding: 10px;
  }
  #sider-menu{
    background-color: rgb(217, 245, 245);
    padding: 10px;
  }
  #article-content img{
    width: 100%;
    margin: auto;
  }
}