  }
  img.show-filter-button:hover{
    background-color: #eee;
  }
  #filter-head .filter-selected{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap-reverse;
  }
  #filter-head .filter-selected>div{
    margin: 5px 10px 0 0;
    border: solid 1px #ddd;
    padding: 2px;
    border-radius: 3px;
  }
  .show-filter{
    display: block;
  }
  .hide-filter{
    display: none;
  }
  .filter-condition{
    display: flex;
    justify-content: flex-start;
  }
  .filter-condition>div:nth-child(1){
    flex:1;
    color: #999;
    font-weight: bolder;
    margin: 5px 10px 0 0;
     
  }
  .filter-condition>div:nth-child(2){
    flex:9;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .filter-condition>div:nth-child(2)>div{
    padding: 2px;
    border-radius: 2px;
    margin: 5px 10px 0 0;

  }
  .filter-condition>div:nth-child(2)>div:hover{
    cursor: pointer;
  }
  .selected-filter-item{
    position: relative;
  }
  
  .selected-filter-item:hover{
    cursor: pointer;
  }
 
  .rm-button img{
    display: block;
    width: 15px
  }
  .selected-option{
    color: #1246db;
    font-weight: bolder;
  }
