@media screen and (min-width: 670px){
  .rm-button{
    display: none;
    position: absolute;
    top:-5px;
    right: -5px;
    z-index: 10000;
  }
  .selected-filter-item:hover .rm-button{
    display: block;
  } 
}
@media screen and (max-width: 670px){
  .rm-button{
    display: block;
    position: absolute;
    top:-5px;
    right: -5px;
    z-index: 10000;
  }
}
