.topic-item{
  padding: 10px;
}
.sub-topic-container{
  position: relative;
  padding: 0 50px;

}
.sub-line{
  position: absolute;
  top:0;
  height: 100%;
  width: 3px;
  background-color: #52addd;
}
.sub-topic-item{
  position: relative;
  left: -8px;
  margin: 17px 0px;
  padding: 0px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.sub-topic-item>div{
  margin: 0 10px;
}
.sub-topic-description{
  padding: 5px;
  color: #888
}
.sub-topic-title{
  width: fit-content;
  color:white;
  font-weight: bold;
  padding: 3px;
  background-color: #52addd;
  border: solid 1.2px #52addd;
  border-radius: 4px;
}
.sub-topic-title:hover{
  cursor: pointer;
  color: #52addd;
  background-color: white;
}
.sub-topic-time{
  color: #aaa;
}

@media screen and (min-width: 670px){
  .topic-title{
    padding: 5px 10px;
    position: sticky;
    top: 75px;
    z-index: 100;
    background-image: linear-gradient(to bottom right,rgb(29, 243, 225),rgb(144, 218, 231),rgb(210, 248, 248));
  }
}

@media screen and (max-width: 670px){
  .topic-title{
    padding: 5px 10px;
    position: sticky;
    top: 0px;
    z-index: 100;
    background-image: linear-gradient(to bottom right,rgb(29, 243, 225),rgb(144, 218, 231),rgb(210, 248, 248));
  }
}